/*
    STYLING FOR EXPLORE.JS
 */

.explorepage {
    width: 100%;
    height: auto;
    min-height: calc(100vh);
    background-color: rgb(21, 32, 43);
}

.container-explore {
    display: flex;
    flex-direction: row;
}

.explore-section {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.explore-content-div {
    width: calc(40%);
    border: 1px solid white;
    border-top: none;
    border-bottom: none;
    height: auto;
    min-height: calc(100vh);
    height: auto;
    display: flex;
    flex-direction: column;   
}

.create-section {
    position: absolute;
    left: calc(18vw);
    top: calc(9vh);
    padding: calc(0.2vw);
    border-radius: calc(0.7vw);
    transition: 0.3s;
}

.explore-header {
    border-bottom: 1px solid white;
    color: white;
    display: flex;
    flex-direction: row;
}

.explore-header h2 {
    font-family: 'Lato', sans-serif;
    margin-bottom: calc(0.7vh);
    margin-top: calc(1vh);
    margin-left: calc(0.7vw);
    font-size: calc(1.5vw);
}

.explore-div {
    width: calc(40%);
    border: 1px solid white;
    border-top: none;
    border-bottom: none;
    height: auto;
    min-height: calc(100vh);
    display: flex;
    flex-direction: column;
}

/* STYLING FOR DRAWERITEM.JS IN THE COMPONENTS FOLDER */
.right {
    width: calc(15vw);
}

/* Searchbar for searching for users */
.searchbar-section {
    position: absolute;
    right: calc(6vw);
    top: calc(5.5vh);
    height: calc(8vh);
    box-shadow: 2px 2px 10px rgb(0, 0, 0);
    border-radius: calc(1rem);
    padding: calc(1vw);
    background-color: rgb(29, 49, 70);
}

@media screen and (max-width: 768px) {
    .explore-content-div {
        width: calc(100%);
        border: none;
    }   

    .suggest-follow h1 {
        margin-top: calc(2.5vh);
        font-size: calc(5.5vw);
        width: calc(90%);
    }

    .explore-header h2 {
        font-family: 'Lato', sans-serif;
        margin-bottom: calc(1vh);
        margin-top: calc(1.8vh);
        margin-left: calc(0.9vw);
        font-size: calc(5.5vw);
    }
    
    .searchbar-section {
        position: absolute;
        right: calc(6vw);
        top: calc(1vh);
        height: 0;
        box-shadow: none;
        border-radius: calc(1rem);
        background-color: transparent;
    }
}